import React from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import { useState } from 'react';
import { useEffect } from 'react';
import Contact from './component/contact';
import Footer from './component/footer';
import Header from './component/header';
import { articles } from './component/product/data';
import Product from './component/product';
import './App.css'
import About from './component/about';
const Home = () => {
  const [APIDATA, setAPIDATA] = useState([])
  useEffect(() => setAPIDATA(articles), [])
  return (<div className='mainDiv'>
    <HashRouter>
      <Header />
      <Switch>
        <Route path='/about' component={About}></Route>
        <Route path='/contact' component={Contact}></Route>
        <Route path='/product' component={Product}></Route>
        <div className='newsDiv'>
          {APIDATA.map(news => <Tabs {...news}></Tabs>)}
        </div>
      </Switch>
      <Footer />
    </HashRouter>
  </div>);
}
export default Home;






const Tabs = ({ author, title, description, url, urlToImage, publishedAt, content }) => {
  return (
    <div className='newsTab'>
      <div className='imgholder'>
       <div className='imgDiv'> <img src={urlToImage}></img></div>
        <span>{author}</span><span>{publishedAt}</span>
      </div>
      <div className='headingHolder'>
        <h2>{title}</h2>
        <h4>{description}</h4>
      </div>
      <div className='contentHolder'>
        <p>{content}</p>
        <button onClick={() => window.open(url)}>Learn More</button>
      </div>
    </div>
  );
}
