import { Link } from 'react-router-dom';
import Styles from "../header/index.module.css"
const Header = () => {
    return (<nav className={Styles.nav}>
        <div className={Styles.company}>
           News World
        </div>
        <div className={Styles.menu}>
            <Link to='/' style={{textDecoration:"none"}}>  <div className={Styles.menuItem}>Home</div></Link>
            <Link to='/about' style={{textDecoration:"none"}}>  <div className={Styles.menuItem}>About</div></Link>
            <Link to='/product' style={{textDecoration:"none"}}>  <div className={Styles.menuItem}>Products</div></Link>
            <Link to='/contact' style={{textDecoration:"none"}}>  <div className={Styles.menuItem}>Contact</div></Link>
        </div>
    </nav>);
}

export default Header;