import { Restaurant } from "./data";
import { useState } from "react";
import { useEffect } from "react";
import Styles from '../product/index.module.css'
const Product = () => {
    const [tableData, settableData] = useState([])
    const [rendering, setrendering] = useState({ first: 0, second: 0, third: 1 })
    useEffect(() => {
        let allchecky = document.getElementsByClassName("checky");
        allchecky[0].checked = 1;
        allchecky[1].checked = 1;
        for (let i = 0; i < Restaurant.length; i++) {
            Restaurant[i].id = i + 1
        }
        settableData(Restaurant.slice(0, 10))
        console.log(Restaurant)
    }, [])


    const handelNext = () => {
        document.getElementById('previous').disabled = false
        var lastCell = document.getElementsByTagName('tr')
        var lastData = parseInt(lastCell[9].firstElementChild.childNodes[0].data)
        console.log(lastData)
        if (lastData == Restaurant.length - 1) {
            document.getElementById('next').disabled = true
        } else {
            settableData(Restaurant.slice(lastData + 1, lastData + 11))
        }
    }




    const handelPrevious = () => {
        document.getElementById('next').disabled = false
        var firstCell = document.getElementsByTagName('td')
        var firstData = parseInt(firstCell[0].innerText)
        console.log(firstData)
        if (firstData == 1) {
            document.getElementById('previous').disabled = true
        }
        else {
            settableData(Restaurant.slice(firstData - 11, firstData - 1))
        }
    }

    const handleCheck = () => {
        let allchecky = document.getElementsByClassName("checky");
        if ((allchecky[0].checked && !allchecky[1].checked) || (allchecky[1].checked && !allchecky[0].checked)) {
            if (allchecky[0].checked && !allchecky[1].checked) setrendering({ first: 1, second: 0, third: 0 })
            else setrendering({ first: 0, second: 1, third: 0 });
        }
        else setrendering({ first: 0, second: 0, third: 1 });
    }


    return (<div className={Styles.main}>
        <div className={Styles.naviDiv}>
            <div>Dispay with cuisine only<input type={"checkbox"} class="checky" value='all' onClick={() => handleCheck()} /></div>
            <div>Dispay with city only<input type={"checkbox"} class="checky" value='removecuisine' onClick={() => handleCheck()} /></div>
        </div>
        <div className={Styles.tableDiv}>
            <table>
                <tr>
                    <th className={Styles.thead}>S.No.</th>
                    <th className={Styles.thead}>Name</th>
                    <th className={Styles.thead}>City</th>
                    <th className={Styles.thead}>Cuisine</th>
                </tr>
                <tbody>
                    {rendering.first ? tableData.map(item => <DisplayCuisine{...item} />):""}
                    {rendering.second ? tableData.map(item => <DisplayCity{...item} />):""}
                    {rendering.third ? tableData.map(item => <DisplayAll{...item} />):""}
                </tbody>
            </table>
            <div className={Styles.BtnDiv}>
                <button onClick={() => handelPrevious()} id="previous">Previous</button>
                <button onClick={() => handelNext()} id='next'>Next</button>
            </div>
        </div>
    </div>);
}

export default Product;
const DisplayAll = ({ id, name, city, cuisine }) => {
    return (
        <tr>
            <td className='SNo'>{id}</td>
            <td className='name'>{name}</td>
            <td className='city'>{city}</td>
            <td className='cuisine'>{cuisine}</td>
        </tr>
    );
}
const DisplayCity = ({ id, name, city }) => {
    return (
        <tr>
            <td className='SNo'>{id}</td>
            <td className='name'>{name}</td>
            <td className='city'>{city}</td>
        </tr>
    );
}
const DisplayCuisine = ({ id, name, cuisine }) => {
    return (
        <tr>
            <td className='SNo'>{id}</td>
            <td className='name'>{name}</td>
            <td></td>
            <td className='cuisine'>{cuisine}</td>
        </tr>
    );
}

