import Styles from "../contact/index.module.css"
const Contact = () => {
    return (
        <div className={Styles.mainDiv}>
            <div>
                <h1>Contact Us</h1>
                <p>We'd love to get in touch and learn more about you. So, send us a message and we'll reply as fast as we can.</p>
            </div>
            <form>
                <div>
                    <input type={'text'} placeholder="First Name" />
                    <input type={'text'} placeholder="Last Name" />
                </div>
                <div>
                    <input type={'number'} placeholder="Phone Number" />
                    <input type={"email"} placeholder="Email" />
                    <input type="textarea"  placeholder="Message" />
                </div>
                <input className={Styles.btn} type={"submit"} value='Send'/>
            </form>
        </div>
    );
}

export default Contact;